import React, { PureComponent } from 'react'
import BtnLang from './BtnLang'

export class Nav extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            lang: 'EN'
        };
    }
    
    passData = (passedData) => {

        if(passedData === 'EN'){
            this.setState({ lang: 'PL' });
        }else{
            this.setState({ lang: 'EN' });
        }
    };


   render() {
     return (
      <nav>
        <BtnLang passData={this.passData} lang={this.state.lang} />
      </nav>
    )
  }
}

export default Nav
