import React, { PureComponent } from 'react'


export class BtnLang extends PureComponent {
    constructor(props) {
        super(props);
        this.state = {
            lang: 'EN'
        };
    }
    
    componentDidUpdate() {
        if (this.props.lang === 'PL') {
            this.setState({ lang: 'PL' });
        }else{
            this.setState({ lang: 'EN' });
        }
      }

   render(props) {

    return (
      <>
        <button id='lang' onClick={() => this.props.passData(this.state.lang) }>
            {this.props.lang}
        </button>
      </>
    )
  }
}

export default BtnLang
